/* src/components/ChatWindow.css */

.chatWindow {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: #ffffff;
  border-left: 1px solid #ddd;
}

.chatWindow__header {
  padding: 10px;
  background-color: #f1f1f1;
  border-bottom: 1px solid #ddd;
  display: flex;
  align-items: center;
  font-weight: bold;
}

.chatWindow__header h2 {
  margin: 0;
  font-size: 16px;
}

.chatWindow__body {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
  background-color: #fafafa;
}

.message {
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 10px;
  max-width: 60%;
  background-color: #e0e0e0;
  display: inline-block;
  clear: both;
}

.message.own {
  background-color: #cfe2f3;
  align-self: flex-end;
  text-align: right;
}

.message:not(.own) {
  background-color: #e6e6e6;
  align-self: flex-start;
  text-align: left;
}

.chatWindow__footer {
  padding: 10px;
  border-top: 1px solid #ddd;
  display: flex;
  align-items: center;
  background-color: #ffffff;
}

.chatWindow__footer form {
  flex: 1;
  display: flex;
  align-items: center;
}

.chatWindow__footer input {
  flex: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 20px;
  margin-right: 10px;
  font-size: 14px;
}

.chatWindow__footer button {
  background-color: #007bff;
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
  cursor: pointer;
  color: #ffffff;
  font-size: 14px;
}

.chatWindow__footer button:hover {
  background-color: #0056b3;
}

.chatWindow__footer svg {
  margin-right: 10px;
}
