.app {
  display: flex;
  height: 100vh;
}

.sidebar {
  width: 50%;
  background-color: #f0f0f0;
  border-right: 1px solid #ddd;
  display: flex;
  flex-direction: column;
}

.chatWindow {
  width: 50%;
  display: flex;
  flex-direction: column;
}

.chatWindow__header {
  padding: 20px;
  background-color: #f6f6f6;
  border-bottom: 1px solid #ddd;
}

.chatWindow__body {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 20px;
  background-color: #dcf8c6;
  overflow-y: auto;
}

.chatWindow__messages {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 60px; /* Adjust based on the height of your MessageInput */
}

.chatWindow__message {
  max-width: 60%;
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 10px;
}

.chatWindow__message--sent {
  align-self: flex-end;
  background-color: #dcf8c6;
  color: #000;
  border-radius: 10px 10px 0 10px;
}

.chatWindow__message--received {
  align-self: flex-start;
  background-color: #fff;
  color: #000;
  border-radius: 10px 10px 10px 0;
}

.messageInput {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #fff;
  border-top: 1px solid #ddd;
  display: flex;
  align-items: center;
  padding: 10px;
}
