.sidebar {
  width: 300px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  border-right: 1px solid #ddd;
}

.sidebar__header {
  padding: 16px;
  border-bottom: 1px solid #ddd;
  background-color: #f8f9fa;
  display: flex;
  align-items: center;
}

.sidebar__header h2 {
  margin: 0;
}

.sidebar__search {
  padding: 16px;
  border-bottom: 1px solid #ddd;
  display: flex;
  align-items: center;
}

.sidebar__search input {
  width: 100%;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.sidebar__chats {
  flex: 1;
  overflow-y: auto;
}

.sidebar__chat {
  padding: 16px;
  border-bottom: 1px solid #ddd;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.sidebar__chat:hover {
  background-color: #f0f0f0;
}

.sidebar__chatInfo {
  margin-left: 8px;
}
